.navbar{
  background: rgb(30, 30, 30);
  height: 5rem ;
  width: 100vw;
  margin: 0 auto;
  padding: 1rem 2rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: sticky;
  top: 0;
  z-index: 3;

}

.desktopmenu{
  margin: 1rem;
  cursor: pointer;
}
.desktopmenu:hover{
  color: yellow;
  padding-bottom: 0.5rem;
  border-bottom: 3px solid yellow;

}
.desktopmenubtn{
  background: white;
  color: black;
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 1rem;
  border-radius: 2rem;
}
.desktopmenuimg{
  object-fit: cover;
  height: 1rem;
  width: 3rem;
  margin: 1rem;
}